import React, { useEffect, useRef, useState } from 'react';
import { AppProps } from 'next/app';
import ReactGA from 'react-ga';
import '../styles/tailwind.css';
import { IconConfig } from '@juuyan.org/ui';
import { icons } from '../styles/icons';
import { Cursor, Loading, Header, CursorSetter, AppContext } from '../components';
import { useSpring, config } from 'react-spring';
import { useDetectTouchScreen } from '@juuyan.org/utility';
import classNames from 'classnames';

// ReactGA.initialize('UA-167143117-1');

export default ({ Component, pageProps }: AppProps) => {
  const [isTouchScreen, setIsTouchScreen] = useState(undefined);
  const [loaded, setLoaded] = useState(false);
  const [scroll, setScroll] = useSpring(() => ({
    config: config.stiff,
    scroll: 0,
    onChange: (props) => {
      console.log(props);
      window.scroll(0, props.scroll);
    },
  }));

  const cursorCoordRef = useRef<{ x: number; y: number }>({ x: 0, y: 0 });
  const cursorSetterRef = useRef<CursorSetter>((x, y) => {
    cursorCoordRef.current = { x, y };
  });
  useDetectTouchScreen((isTS) => {
    setIsTouchScreen(isTS);
  });

  useEffect(() => {
    if (loaded) {
      setScroll({ scroll: window.innerHeight * 0.8 });
      // window.scrollBy(0, window.innerHeight * 0.8);
      console.log('loaded');
    }
  }, [loaded]);

  return (
    <>
      <Loading onAssetsLoad={() => setLoaded(true)} />
      {isTouchScreen === false && (
        <Cursor cursorSetterRef={cursorSetterRef} cursorCoordRef={cursorCoordRef} />
      )}
      {isTouchScreen !== undefined && (
        <AppContext.Provider
          value={{
            cursorCoordRef,
            cursorSetterRef,
          }}
        >
          <IconConfig.Provider value={icons}>
            {loaded && <Header />}
            <div className={classNames(!loaded && 'fixed inset-0 overflow-hidden')}>
              <Component {...pageProps} />
            </div>
          </IconConfig.Provider>
        </AppContext.Provider>
      )}
    </>
  );
};
